* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@import url('https://fonts.googleapis.com/css2?family=Mochiy+Pop+One&family=Pacifico&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap');
.pacifico-regular {
    font-family: "Pacifico", cursive;
    font-weight: 400;
    font-style: normal;
  }
::selection {
  color: var(--cor-branco);
  background: var(--cor-preta);
}

:root {
  --cor-preta: #333333;
  --cor-branco: #ffffff;
  --cor-branco-secundaria: linear-gradient(271deg, #ffffff 0%, #f1eef9 100%);
  --cor-cinza: rgb(90, 90, 90);
  --cor-gif: 0px 0px 20px 0px #0000000e;
  --habCard-shadow: 0px 0px 12px #0000004d;
  --cor-destaque: #4D54FF;
  --cor-footer: #d8d9e1;
  --cor-teste: rgb(255, 255, 255);
  --cor-teste2: rgb(232, 234, 241);
  --tamanho-secao: 1024px;
  --header-shadow: 0px 10px 25px 0px rgba(122, 175, 255, 0.40);
}

.dark-theme {
  --cor-preta: #ffffff;
  --cor-branco: #333333;
  --cor-branco-secundaria: #0d0d10;
  --cor-cinza: rgb(201, 201, 201);
  --cor-gif: 0px 0px 20px #4d53ff1c;
  --habCard-shadow: 0px 0px 12px #778bff63;
  --cor-destaque: #4D54FF;
  --cor-footer: #060608;
  --cor-teste: #09090b;
  --cor-teste2: rgba(255, 255, 255, 0.03);
  --header-shadow: 0px 10px 25px 0px rgba(0, 0, 0, 0.45);
}

.span {
  background: linear-gradient(-90deg, rgba(77, 84, 255, 1) 0%, rgb(146, 170, 255) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: var(--cor-destaque);
}

.span::selection {
  -webkit-text-fill-color: var(--cor-branco);
  background: var(--cor-preta);
}

html {
  scroll-behavior: smooth;
}

body {
  background: var(--cor-branco-secundaria);
}

a, h1, h2 {
  font-family: sans-serif;
  font-weight: bold;
  color: var(--cor-preta);
}
li, p{
  font-family: sans-serif;
  font-weight: 400;
  color: var(--cor-preta);
}
a {
  color: var(--cor-preta);
  text-decoration: none;
}

.textAnim {
  opacity: 0;
  font: 700 max(36px,3vw) /max(48px,5vw) sans-serif;
}

.textAnim2 {
  transform: translateY(40px);
}



